        <view class="item-icon">
          <image
            :src="dataSource.cate_image"
            mode="widthFix"
            style="width: 100%"
          />
        </view>
        <view class="item-content">
          <view class="item-title">
            {{ dataSource.cate_name }}
          </view>
          <view class="item-desc" v-if="dataSource.title">
            <view class="desc_icon">
              <image
                :src="getIcon(dataSource)"
                mode="widthFix"
                style="width: 100%"
              />
            </view>
            {{ dataSource.title }}
          </view>
          <view class="item-price">
            <view class="price_icon">
              <image
                src="@/static/price-icon.svg"
                mode="widthFix"
                style="width: 100%"
              />
            </view>
            ${{ dataSource.price }}
          </view>
