<script setup>
// 导入所需图片
// 搜索框的图标
import topPicks_titleSettingIcon from "@/static/home/topPicks_titleSettingIcon.png"
import img_dollor_money from "@/static/explore/general/dollor_money.png"

import img_request_locate from "@/static/explore/general/request_locate.png"
import img_request_checked from "@/static/explore/general/request_checked.png"
import img_request_arrowGo from "@/static/explore/general/request_arrowGo.png"

import img_offering_locate from "@/static/explore/general/offering_locate.png"
import img_offering_checked from "@/static/explore/general/offering_checked.png"
import img_offering_arrowGo from "@/static/explore/general/offering_arrowGo.png"

// requests的请求列表图片
import img_request_delivery from "@/static/explore/requests/request_delivery.png"
import img_request_apparel from "@/static/explore/requests/request_apparel.png"
import img_request_appliance from "@/static/explore/requests/request_appliance.png"
import img_request_watchAndJewllery from "@/static/explore/requests/request_watchAndJewllery.png"
import img_request_tutor from "@/static/explore/requests/request_tutor.png"
// offerings的请求列表图片
import img_offering_delivery from "@/static/explore/offerings/offering_delivery.png"
import img_offering_apparel from "@/static/explore/offerings/offering_apparel.png"
import img_offering_appliance from "@/static/explore/offerings/offering_appliance.png"
import img_offering_watchAndJewllery from "@/static/explore/offerings/offering_watchAndJewllery.png"
import img_offering_tutor from "@/static/explore/offerings/offering_tutor.png"
import { indexOrderDetail } from "@/api/totalApi"
import SafeTopContainer from "@/components/SafeTopContainer.vue"
import { useNavigate } from "@/hooks/useNavigate"

const dataSource = ref()

const isMatchMode = ref(false)

const handleClickLeft = () => {
  // 跳转到上一页
  uni.navigateBack()
}

const activeTab = ref("Requests")

const getIcon = (item) => {
  // 提供
  if (item.methodtype == 1) {
    if (item.method == 1 || item.method == 3) {
      return img_request_locate
    } else {
      return img_request_checked
    }
  }
  // 需求
  else if (item.methodtype == 2) {
    if (item.method == 2 || item.method == 4) {
      return img_offering_locate
    } else {
      return img_offering_checked
    }
  }
}

const handleClickConfirmMatch = () => {
  useNavigate("/pages/tracking/confirm")
}

onLoad(async (option) => {
  if (option.id) {
    const r = await indexOrderDetail({ id: option.id })
    dataSource.value = r.data
  }

  if (option.match) {
    isMatchMode.value = true
  }
})
</script>
